.o-question-container {
  padding: 1em 0;
  position: relative;
}

.o-question-container:not(.no-separator) {
  border-bottom: 1px solid #D0DAE6;
}

.o-question-container:last-child {
  padding-bottom: 0;
}

.surroundcontent > div:last-child {
  border-bottom: none;
}

.sidebyside .o-question-core {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.o-question-information {
  margin-bottom: 0.5em;
}

.sidebyside .o-question-errorandinstruction {
  width: 40%;
  padding-right: 1em;
  box-sizing: border-box;
}

.sidebyside .o-question-information {
  box-sizing: border-box;
  padding-right: 1em;
  margin-bottom: 0;
  flex: 0 0 40%;
}

.sidebyside .o-question-response {
  flex: 1;
}